.advantages__list-wrapper svg {
  display: block;
  margin: 0 0 20px;
}
@media (max-width: 1023px) {
  .advantages__list-wrapper svg {
    margin: 0 0 20px -4px;
    height: 26px;
  }
}
.advantages__list-wrapper h3 {
  margin: 0 0 10px;
  font-weight: 500;
  font-size: 18px;
  line-height: 140%;
}
@media (max-width: 767px) {
  .advantages__list-wrapper h3 {
    margin: 0 0 8px;
  }
}
.advantages__list-wrapper p {
  margin: 0;
}

.catalog {
  padding: 85px 0 120px;
}
@media (max-width: 1023px) {
  .catalog {
    padding: 89px 0 100px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 56px 0 65px;
  }
}

.catalog__title {
  margin: 0 0 50px;
  text-align: center;
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .catalog__title {
    font-size: 26px;
  }
}
@media (max-width: 767px) {
  .catalog__title {
    margin: 0 0 30px;
    font-size: 24px;
  }
}

.catalog__list-wrapper {
  padding: 0 15px;
  display: flex;
  justify-content: center;
}
@media (max-width: 767px) {
  .catalog__list-wrapper {
    padding: 0 10px;
  }
}
.catalog__list-wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
@media (max-width: 1023px) {
  .catalog__list-wrapper ul {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 767px) {
  .catalog__list-wrapper ul {
    grid-template-columns: 1fr;
  }
}

.booking {
  position: relative;
  min-height: 555px;
  display: flex;
  align-items: center;
}

.booking__background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.booking__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.booking__center-container {
  display: flex;
  justify-content: center;
}

.booking__container {
  padding: 50px 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
}
@media (max-width: 1023px) {
  .booking__container {
    padding: 88px 125px 100px;
    flex-direction: column;
    gap: 52px;
  }
}
@media (max-width: 767px) {
  .booking__container {
    width: 100%;
    padding: 60px 0 65px;
    gap: 34px;
  }
}

.booking__text {
  margin: 0 0 8px;
}
@media (max-width: 1023px) {
  .booking__text {
    margin: 0;
    text-align: center;
  }
}

.booking__text h2 {
  margin: 0 0 8px;
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .booking__text h2 {
    margin: 0 0 5px;
    font-size: 34px;
  }
}
@media (max-width: 767px) {
  .booking__text h2 {
    margin: 0 0 12px;
    font-size: 24px;
    line-height: 130%;
  }
}

.booking__text p {
  margin: 0;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 767px) {
  .booking__text p {
    font-size: 16px;
    line-height: 140%;
  }
}

.booking__form-wrapper {
  flex-shrink: 0;
  width: 403px;
}
@media (max-width: 1023px) {
  .booking__form-wrapper {
    width: auto;
    padding: 0 12px;
  }
}
@media (max-width: 767px) {
  .booking__form-wrapper {
    padding: 0;
  }
}

